body {
    background-color: #f1f1f1;
    color: #333;
}

#regForm {
    background-color: #ffffff;
    margin: 100px auto;
    font-family: Raleway;
    padding: 40px;
    width: 70%;
    min-width: 300px;
}

h1 {
    text-align: center;
}

input,
textarea,
select {
    padding: 10px;
    width: 100%;
    height: 100%;
    font-size: 17px;
    font-family: Raleway;
    border: 1px solid #aaaaaa;
    background-color: #fff;
}

input:-moz-read-only {
    cursor: not-allowed;
    background: #ddd;
}

input:read-only {
    cursor: not-allowed;
    background: #ddd;
}

button#nextBtn:disabled {
    opacity: 0.4;
    cursor: not-allowed;
}

/* Mark input boxes that gets an error on validation: */

input.invalid {
    background-color: #ffdddd;
}

/* Hide all steps by default: */

.tab {
    /* display: none; */
    padding: 40px;
    background-color: #fff;
    /* height: calc(100vh - 55px); */
}

button {
    background-color: #4CAF50;
    color: #ffffff;
    border: none;
    padding: 10px 20px;
    font-size: 17px;
    font-family: Raleway;
    cursor: pointer;
}

button:hover {
    opacity: 0.8;
}

#prevBtn {
    background-color: #bbbbbb;
}

/* Make circles that indicate the steps of the form: */

.step {
    height: 15px;
    width: 15px;
    margin: 0 2px;
    background-color: #bbbbbb;
    border: none;
    border-radius: 50%;
    display: inline-block;
    opacity: 0.5;
}

.step.active {
    opacity: 1;
}

/* Mark the steps that are finished and valid: */

.step.finish {
    background-color: #4CAF50;
}

#loggedinUserDetail {
    text-align: right;
    width: 100%;
}

#loggedinUserDetail .welcome {
    display: inline-block;
    padding: 10px;
    text-transform: uppercase;
}

#loggedinUserDetail .logout {
    display: inline-block;
    padding: 10px;
}

#loggedinUserDetail .logout a {
    color: #095ee6;
    cursor: pointer;
}

#loader {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    text-align: center;
    background: rgba(255, 255, 255, 0.7);
    z-index: 9999;
}

#loader img {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    left: 0;
}

/* CSS for Custom Radio */

/* The container */

.custom-radio {
    display: block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 22px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

/* Hide the browser's default radio button */

.custom-radio input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    width: 0px;
}

/* Create a custom radio button */

.checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: #eee;
    border-radius: 50%;
}

/* On mouse-over, add a grey background color */

.custom-radio:hover input~.checkmark {
    background-color: #ccc;
}

/* When the radio button is checked, add a blue background */

.custom-radio input:checked~.checkmark {
    background-color: #2196F3;
}

/* Create the indicator (the dot/circle - hidden when not checked) */

.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

/* Show the indicator (dot/circle) when checked */

.custom-radio input:checked~.checkmark:after {
    display: block;
}

/* Style the indicator (dot/circle) */

.custom-radio .checkmark:after {
    top: 9px;
    left: 9px;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: white;
}

.hide {
    display: none;
}

.show {
    display: block;
}
